@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500&display=swap');
body{
  font-family: 'Open Sans', sans-serif;
  overflow-x:hidden;
  
}
html{
	scroll-behavior: smooth;
}
.gameImg{
	position:absolute;
	border-radius:10px;
	width:150px;
	filter:saturate(150%);
	border:2px solid rgba(0,0,0,0.7);
	cursor:pointer;
	filter:brightness(100%);
	transition:0.1s all;
}
.gameName{
	position:absolute;
	width:150px;
	text-align:center;
	font-size:20px;
	color:rgba(0,0,0,0.7);
	cursor:pointer;
}
.gameDate{
	position:absolute;
	width:150px;
	text-align:center;
	font-size:18px;
	color:rgba(0,0,0,0.5);
	cursor:pointer;
}
.intro{
	font-size:80px;
	position:absolute;
	width:100%;
	text-align:center;
	top:13%;
}
.featured{
	font-size:60px;
	color:rgba(0,0,0,0.7);
	position:absolute;
	left:24%;
	top:30%;
	text-decoration:underline;
	text-decoration-thickness:3px;
	text-underline-position:under;
}
.tag{
	position:absolute;
	height:23px;
	color:white;
	font-size:15px;
	text-align:center;
	border-radius:5px;
	padding:3px;
	font-weight:500;
}
.info{
	position:absolute;
	font-size:30px;
	left:24%;
	top:78%;
	color:rgba(0,0,0,0.8);
}
.info2{
	position:absolute;
