}
.info3{
	position:absolute;
	font-size:30px;
	left:24%;
	top:90%;
	color:rgba(0,0,0,0.8);
}
.anim{
    background: linear-gradient(
        to right,
        #7953cd 20%,
        #00affa 30%,
        #0190cd 70%,
        #764ada 80%
    );
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    text-fill-color: transparent;
    background-size: 500% auto;
    animation: textShine 5s ease-in-out infinite alternate;
}
@keyframes textShine {
    0% {
        background-position: 0% 50%;
    }
    100% {
        background-position: 100% 50%;
    }
}
.downloadsBar{
	display:flex;
	justify-content:center;
	align-items:center;
	position:absolute;
	width:20%;
	top:148%;
	height:10%;
	left:28%;
	border:2px solid #00affa;
	color:white;
	font-size:70px;
	border-radius:50px;
	overflow:hidden;
}
.downloadsBarText{
	display:flex;
	justify-content:center;
	align-items:center;
	position:absolute;
	width:100%;
	top:0%;
	height:100%;
	left:0%;
	
	color:white;
	font-size:70px;
	border-radius:50px;
	
}
.downloadsBarFill{
	background-color:#00affa;
	position:absolute;
	width:0%;
	top:0%;
	height:100%;
	left:0%;
	border-radius:50px;
	
}
.active{
	animation:fill 4s cubic-bezier(0, 1.06, 0.49, 1) 1;
	animation-fill-mode:forwards;
	animation-delay:0.5s;
}
.downloadsBarLabel{
	position:absolute;
	top:160%;
	font-size:40px;
	color:black;
	width:20%;
	height:10%;
	left:28%;
	text-align:center;
}
@keyframes fill{
	0%{width:0%;}
	100%{width:100%;}
}
